import { Controller, Commands } from '@ali/4ever-cangjie';
import TableRow from '../../mo/models/tableRow';
import { MIN_ROW_HEIGHT } from '../constants';

export default function resizeRowsHeight(controller: Controller, tableRow: TableRow, newHeight: number) {
  return controller.command(Commands.setNodeByKey, tableRow.key, {
    data: {
      ...tableRow.data,
      h: Math.max(newHeight, MIN_ROW_HEIGHT),
    },
  });
}
